<template>
    <section style="width:100%">
        <div class="row form-group"></div>
        <div class="row form-group">
            <div class="col-md-6">
                <button type="button" class="btn btn-primary" @click="resetForm()">New Quize</button>
            </div>
            
        </div>
        <form >
            <div class="row form-group">
                <div class="col-md-6">
                    <label >Quize Name</label>
                    <input type="text" class="form-control" ref="name" id="name" v-model="quizename" aria-describedby="emailHelp" placeholder="Enter name">
                    <small id="nameHelp" class="form-text text-muted">Max length 50 character</small>
                    
                </div>
               
            </div>
            <div class="row form-group">
                <div class="col-md-6">
                    <button type="button" @click.prevent="saveQuize()" class="btn btn-primary">Save</button>
                </div>
            </div>
        </form>
       
        <div id="question" v-if="show">
            <form>
                <div class="row form-group" id="">
                    <div class="col-md-6">
                        <label >Question</label>
                        <input type="text" class="form-control" ref="question" id="question" v-model="question" placeholder="Enter Question">
                        <small id="nameHelp" class="form-text text-muted">Max length 50 character</small>
                    </div>
                    <div class="col-md-6">
                        <label >Answer</label>
                        <input type="text" class="form-control" ref="answer" id="answer" v-model="answer"  placeholder="Enter Answer">
                        <small id="nameHelp" class="form-text text-muted">Max length 50 character</small>
                    </div>
                    
                </div>
                <div class="row form-group">
                    <div class="col-md-6">
                        <button type="submit" class="btn btn-primary" @click.prevent="addQuestion()">Add Question</button>
        
                    </div>
                </div>
            </form>
            
        </div>
    </section>
</template>


<script>
import {HTTP} from './../http-common.js';
import axios from 'axios';
export default {
    data(){
        return{
            quizename:"",
            show:false,
            question:"",
            answer:"",
            quizeId:0,
            auth:false
        }
    },
    methods:{
        resetForm(){
            this.quizename="";
            this.show=false;
            this.answer="";
            this.question="";
        },
        saveQuize(){
            //alert(this.$refs.name.value)
            if(localStorage.quizeId==null)
                localStorage.quizeId=0;
            let quizeId=Number( localStorage.quizeId)+1;
            localStorage.quizeId=quizeId
            let quizes=[];//alert(localStorage.getItem("quizes"))
            if(localStorage.getItem("quizes")!=null)
            quizes=JSON.parse(localStorage.getItem("quizes"));
            quizes.push({Id:quizeId,Name:this.$refs.name.value,userId:1})
            localStorage.setItem("quizes",JSON.stringify( quizes))
            this.show=true;
            this.quizeId=quizeId;
            console.log( localStorage.quizes)
            // HTTP.post("/quize",{Name:this.$refs.name.value,userId:1} )
            // .then(response => {
            //     console.log(response)
               
            //     this.show=true;
               
            // })
            // .catch(e => {
            //     console.log(e)
            
            // })
        },
        addQuestion(){
            //alert(this.$refs.name.value)
            // this.show=true;
             let quizes=[],quize_question=[];//alert(localStorage.getItem("quizes"))
             //alert(localStorage.questionId)
             if(localStorage.questionId==null || localStorage.questionId==undefined){
                localStorage.questionId=0
                // alert(localStorage.questionId)
             }
            let questionId=Number(localStorage.questionId)+1;
            localStorage.questionId=questionId;
            if(localStorage.getItem("quize_question")!=null)
                quize_question=JSON.parse(localStorage.getItem("quize_question"));
            quize_question.push({Id:questionId,question:this.$refs.question.value,quizeId:this.quizeId,answer:this.$refs.answer.value})
            localStorage.setItem("quize_question",JSON.stringify( quize_question))
            console.log(localStorage.getItem("quize_question"))
            
            // axios.post('http://localhost:5001/quize', {body:{Name:this.$refs.name.value,userId:1}}, 
            // {headers: {'Access-Control-Allow-Origin': '*',}}
            // ).then(r => {
            
            // console.log('r: ', JSON.stringify(r))
            
            // }
            
            // );


            // HTTP.post("http://localhost:5001/quize/question",{body:{question:this.$refs.question.value,answer:this.$refs.answer.value,quizeId:quizeId}} )
            // .then(response => {
            //     console.log(response)
            //     // if(response.data.statusCode=="200")
            //     // {
                   
                    
            //     // }else{
                   
            //     // }
            //     this.answer="";
            // this.question="";
            //     this.show=true;
               
            // })
            // .catch(e => {
            //     console.log(e)
            
            // })
        }
    }
}
</script>

