<template>
    <div style="width:100%">
        <div class="row">
            <div class="col-md-6" :v-for="q in quizesAnswers">
                <label for="">{{q.questionId}}</label>
                <label for="">{{q.answer}}</label>
            </div>
        </div>
    </div>
</template>


<script>
export default {
    data(){
        return{
            quizesAnswers:[]
        }
    },
    methods:{
        getQuizesAnswers(){
            let quizesAnswers=[];
            if(localStorage.getItem("quizesAnswer")!=null && localStorage.getItem("quizesAnswer")!="")
                quizesAnswers=JSON.parse(localStorage.getItem("quizesAnswer"));
                this.quizesAnswers=quizesAnswers;
          //  alert(localStorage.getItem("quizesAnswer"))
        }
    },
    created(){
        this.getQuizesAnswers();
    }
}
</script>


<style>

</style>





