<template>
    <div style="width:100%">
        <div class="row form-group"></div>
            <div class="row form-group">
                <div class="col-md-4">
                    <label >All Quizes</label>
                    <ul >
                        <li v-for="quize in quizes" :key="quize.Id" @click="showQuizeQuestion(quize.Id)">
                            <a  >{{quize.Name}} </a> 
                            <br>
                            <!-- <button class="btn btn-default" :key="quize.Id" type="button" @click="showQuizeQuestion(quize.Id)">{{quize.Name}}</button> -->
                        </li>
                        <!-- <li><a>Quize1</a> </li>
                        <li><a>Quize2</a></li>
                        <li><a>Quize3</a></li> -->
                    </ul>
                    
                </div>

                <div class="col-md-8">

                    <div  class="row" v-if="question!=''">
                        <div class="col-md-12" >
                            <label for="">{{question}}</label>
                            <input type="text" name="" id="" v-model="answer" class="form-control" style="width: 50%;display: inline;">
                            <button type="button" class="btn btn-primary" style="margin-bottom: 5px" @click="SaveQuestionAnswer()">Answer</button>
                            <button @click="Next()" class="btn btn-primary" :disabled="this.index>=this.quizeQuestions.length-1">Next</button>
                            <button @click="Prev()" class="btn btn-primary" :disabled="this.index==0">Prev</button>
                        </div> 
                    </div>
                </div>
               
            </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            userId:1,
            quizes:[],
            quizeQuestion:{},
            quizeId:0,
            question:"",
            answer:"",
            index:0,
            quizeQuestions:[]
        }
    },
    methods:{
        getAllQuizes(){
            this.index=0;
            localStorage.quizesAnswer="";
            this.quizes=JSON.parse( localStorage.getItem("quizes"));
           // console.log(this.quizes[0].name)
            // axios.get(`http://localhost:5001/quize/`+this.userId)
            // .then(response => {
            // // JSON responses are automatically parsed.
            // this.quizes=response.data;
            // console.log( response.data)
            // })
            // .catch(e => {
            // this.errors.push(e)
            // })
            
        },
        showQuizeQuestion(quizeId){
            this.index=0;
            let quize_question=[];
            localStorage.setItem("quizesAnswer","")
            this.quizeQuestions=[];
            let quizeQuestions=JSON.parse( localStorage.quize_question);
            for(var i=0;i<quizeQuestions.length;i++)
            {
                if(quizeId==quizeQuestions[i].quizeId)
                {
                    this.quizeQuestions.push(quizeQuestions[i])
                    this.quizeQuestion=quizeQuestions[i];
                    this.quizeId=quizeId;
                }
            }
            this.question=this.quizeQuestions[0].question;
        },
        SaveQuestionAnswer(){
            let questionId=this.quizeQuestion.Id;
            //alert(JSON.stringify(localStorage.getItem("quizesAnswer")))
            let quizesAnswers=[];
            if(localStorage.getItem("quizesAnswer")!=null && localStorage.getItem("quizesAnswer")!="")
                quizesAnswers=JSON.parse(localStorage.getItem("quizesAnswer"));
            quizesAnswers.push({quizeId:this.quizeId,questionId:questionId,answer:this.answer})
            localStorage.quizesAnswer=JSON.stringify( quizesAnswers);
            console.log(JSON.stringify(localStorage.quizesAnswer))
        },
        Next(){
            this.index++;
            if(this.index<this.quizeQuestions.length){
                this.quizeQuestion=this.quizeQuestions[this.index]
                this.question=this.quizeQuestions[this.index].question;
                this.answer="";
            }
        },
        Prev(){
            
            if(this.index>0){
                this.index--;
                this.quizeQuestion=this.quizeQuestions[this.index]
                this.question=this.quizeQuestions[this.index].question;
                this.answer="";
            }
        }
    },
    created(){
        this.getAllQuizes();
    }
}
</script>
<style  scoped>

ul{
    list-style-type: none
}

</style>





