<template>
  <div class="container">
    <app-login  @loginPass="loginPass($event)" v-show="!auth"></app-login>
    <div class="row" v-show="auth">
      <app-header></app-header>
    </div>
    <div class="row" v-show="auth">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Header from './component/header'
import Home from './component/Home'
import Login from './component/Login'
export default {
  data(){
    return{
      auth:false
    }
  },
  components:{
    appHeader:Header,
    appHome:Home,
    appLogin:Login
  },
  methods:{
    loginPass(value)
    {
      if(value!=null)
      {
        this.auth=value;
        this.$router.replace('/')
      }
      else{
        this.auth=false;
      }
      console.log(value);
    }
  },
  
  beforeCreate() {
      if (this.$store.getters.Auth == false) {
        this.$router.replace('/Login')
        this.auth=false;
      }
      else{
          this.auth=true
      }
    }
    // created(){
    //     if (this.$store.getters.Auth == false) {
    //      // alert(this.$store.getters.Auth)
    //       this.auth=false;
    //     }
    // }
}
</script>

<style lang="scss">

</style>



